/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/

.widget {
  margin: 0 0 $base__space-unit;

  /* Make sure select elements fit in widgets. */
  select {
    max-width: 100%;
  }
}

/* Search widget. */
.widget_search .search-submit {
  display: none;
}

.inhabitent-business-hours-class {
  .widget-title {
    text-transform: uppercase;
  }
  .day-of-week {
    font-weight: 700;
  }
}



